
.booking-submit .info-list {
  margin-bottom: .14rem;
}
.booking-submit .info-list .btn-item {
    padding-top: .16rem;
    padding-bottom: .16rem;
}
.booking-submit .info-list .btn {
    padding-top: .3em;
    padding-bottom: .3em;
}
.booking-submit .info-list .remark label {
    float: left;
    margin-top: .06rem;
}
.booking-submit .info-list .remark .text-wrap {
    margin-left: .85rem;
}
.booking-submit .info-list .remark .text-wrap textarea {
      width: 100%;
      font-size: .24rem;
}
.booking-submit .class-sel-wrap {
  padding-left: .2rem;
}
.booking-submit .class-sel-wrap .years {
    padding: .2rem;
}
.booking-submit .class-sel-wrap .years li {
      width: 100%;
      color: #888;
      margin-bottom: .1rem;
      padding: .1rem 0;
}
.booking-submit .class-sel-wrap .years li.active {
        color: #3499db;
        border-bottom: 0.02rem solid #3499db;
}
.booking-submit .class-sel-wrap .class-sel-list {
    width: 100%;
}



