
.result {
  padding-bottom: 4rem;
}
.result ul {
    background: #fff;
    padding: 0.3rem;
    margin-bottom: 0.1rem;
}
.result ul li {
      line-height: 0.4rem;
      font-size: 0.3rem;
}
.open {
  background: #eee;
  width: 100%;
  position: fixed;
  bottom: 0;
}
.open .door_btn {
    width: 2rem;
    height: 2rem;
    margin: 1rem auto;
    color: #fff;
    background: url("../../assets/images/signbtns.png") 0 0 no-repeat;
    background-size: cover;
}
.open .door_btn p {
      display: inline-block;
      position: relative;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      font-size: 0.35rem;
}



