
.bottomHandlers{
    width:80%;
    margin:0 auto;
    overflow: hidden;
    margin-top:30px;
}
.left{
    float:left;
}
.right{
    float:right;
}
.contentBox{
    padding:40px 30px 30px 30px;
    margin-bottom:14px;
    border-top:1px solid black;
    border-bottom:1px solid black;
}
.boxTitle{
    margin-bottom:30px;
}
.shadow{
    width:100%;
    height:100%;
    background-color: rgba(0,0,0,0.5);
    position:fixed;
    top:0;
    left:0;
}
.confirmBox{
    width:80%;
    margin:100px auto;
    background-color: white;
    border-radius: 5px;
    padding-bottom:20px;
}
.confirmText{
    padding:20px;
    border-bottom:1px solid #eee;
    margin:0;
}



