.switch {
  width: .52rem;
  height: .3rem;
  position: relative;
  border: 1px solid #dfdfdf;
  background-color: #fdfdfd;
  box-shadow: #dfdfdf 0 0 0 0 inset;
  border-radius: .15rem;
  border-top-left-radius: .15rem;
  border-top-right-radius: .15rem;
  border-bottom-left-radius: .15rem;
  border-bottom-right-radius: .15rem;
  background-clip: content-box;
  display: inline-block;
  -webkit-appearance: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}
.switch:before {
  content: '';
  width: .28rem;
  height: .28rem;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: .14rem;
  background-color: #fff;
  box-shadow: 0 0.01rem 0.03rem rgba(0, 0, 0, 0.4);
}
.switch:checked {
  border-color: #64bd63;
  box-shadow: #64bd63 0 0 0 .16rem inset;
  background-color: #64bd63;
}
.switch:checked:before {
  left: .21rem;
}
.switch.switch-animbg {
  -webkit-transition: background-color ease 0.4s;
  transition: background-color ease 0.4s;
}
.switch.switch-animbg:before {
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
}
.switch.switch-animbg:checked {
  box-shadow: #dfdfdf 0 0 0 0 inset;
  background-color: #64bd63;
  -webkit-transition: border-color 0.4s, background-color ease 0.4s;
  transition: border-color 0.4s, background-color ease 0.4s;
}
.switch.switch-animbg:checked:before {
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
}
.switch.switch-anim {
  -webkit-transition: border cubic-bezier(0, 0, 0, 1) 0.4s, box-shadow cubic-bezier(0, 0, 0, 1) 0.4s;
  transition: border cubic-bezier(0, 0, 0, 1) 0.4s, box-shadow cubic-bezier(0, 0, 0, 1) 0.4s;
}
.switch.switch-anim:before {
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
}
.switch.switch-anim:checked {
  box-shadow: #64bd63 0 0 0 .16rem inset;
  /* background-color: #64bd63; */
  -webkit-transition: border ease 0.4s, box-shadow ease 0.4s, background-color ease 1.2s;
  transition: border ease 0.4s, box-shadow ease 0.4s, background-color ease 1.2s;
}
.switch.switch-anim:checked:before {
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
}
.switch.switch-red:checked {
  border-color: #FE434C;
  box-shadow: #FE434C 0 0 0 .16rem inset;
  background-color: #FE434C;
}
.switch.switch-blue:checked {
  border-color: #4389FE;
  box-shadow: #4389FE 0 0 0 .16rem inset;
  background-color: #4389FE;
}
.switch.switch-green:checked {
  border-color: #66D296;
  box-shadow: #66D296 0 0 0 .16rem inset;
  background-color: #66D296;
}
.switch.switch-orange:checked {
  border-color: #F0A009;
  box-shadow: #F0A009 0 0 0 .16rem inset;
  background-color: #F0A009;
}
.mm-radio, .mm-checkbox {
  position: relative;
}
.mm-radio label, .mm-checkbox label {
    float: none;
    display: inline-block;
    width: 100%;
    padding-right: 58px;
}
.mm-radio.mm-left input[type="radio"], .mm-checkbox.mm-left input[type="checkbox"] {
  left: 20px;
}
.mm-radio.mm-left label, .mm-checkbox.mm-left label {
  padding-left: 58px;
  padding-right: 15px;
}
.mm-radio input[type="radio"], .mm-checkbox input[type="checkbox"] {
  -webkit-appearance: none;
  outline: 0 !important;
  width: 28px;
  border: 0;
  height: 26px;
  display: inline-block;
  position: absolute;
  right: 20px;
  top: 4px;
  background-color: transparent;
}
.mm-radio input[type="radio"][disabled]:before, .mm-checkbox input[type="checkbox"][disabled]:before {
    opacity: .3;
}
.mm-radio input[type="radio"]:before, .mm-checkbox input[type="checkbox"]:before {
    font-family: Muiicons;
    font-weight: normal;
    border-radius: 0;
    font-size: 28px;
    line-height: 1;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    color: #aaaaaa;
    background: none;
}
.mm-radio input[type="radio"]:checked:before, .mm-checkbox input[type="checkbox"]:checked:before {
    color: #3499db;
}
.mm-radio.mm-disabled label, .mm-radio label.mm-disabled, .mm-checkbox.mm-disabled label, .mm-checkbox label.mm-disabled {
  opacity: .4;
}
.mm-radio input[type="radio"]:before {
  content: "\e411";
}
.mm-radio input[type="radio"]:checked:before {
  content: "\e441";
}
.mm-checkbox input[type="checkbox"]:before {
  content: "\e411";
}
.mm-checkbox input[type="checkbox"]:checked:before {
  content: "\e442";
}
