
.toast-wrap {
    background-color:none !important;
    position: fixed;
    top: 0;
    left: 0;
    right:0;
    bottom:0;
    background: rgba(0, 0, 0, 0.4);
}
.toast-wrap .toast-center{
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    margin-left: auto;
    margin-right: auto;
    display: inline-block;
    background: #fff;
    padding: 0 10px;
    font-size: 16px;
    padding: 10px 30px;
    /*border: 2px solid #333;*/
    -webkit-transform: translate3d(-50%,-50%,0);
            transform: translate3d(-50%,-50%,0);
    opacity:.8;
    border-radius:5px;
}



