
.booking-time-sel {
  border-top: 0;
}
.booking-time-sel .time-sel {
    background: #FFF;
    margin-bottom: .2rem;
    padding: .24rem;
}
.booking-time-sel .lab-info {
    font-size: .26rem;
    margin-bottom: .24rem;
    color: #333;
}
.booking-time-sel .lab-info .view-lab {
      font-size: .2rem;
      color: #369;
}
.booking-time-sel .time-tabs {
    border: 1px solid #3499db;
    border-radius: .06rem;
    padding-left: .18rem;
    padding-right: .18rem;
}
.booking-time-sel .time-tabs .tab label {
      color: #888;
      font-size: .3rem;
      padding: .2rem .05rem .2rem .05rem;
}



